<template>
  <div class="py-5">
    <!---------------------------------------->
    <div class="container ad">
      <div class="row justify-content-center">
        <div class="col-md-12">
          <h3 class="text-center">الأعلان</h3>
        </div>
      </div>
      <div class="row justify-content-center p-2">
        <div class="form-group p-2">
          <textarea cols="80" rows="7" class="form-control" v-model="ad"></textarea>
        </div>
      </div>
      <div class="row g-2 justify-content-center">
        <div class="col-md-4">
          <button @click="publishAd" class="btn custom-btn w-100">
            {{ loading ? "publishing..." : "نشر" }}
          </button>
        </div>
        <div class="col-md-4">
          <button
            @click="
              () => {
                ad = '';
                publishAd();
              }
            "
            class="btn btn-danger w-100"
          >
            {{ loading ? "publishing..." : "مسح الأعلان" }}
          </button>
        </div>
      </div>
    </div>
    <!---------------------------------------->
  </div>
</template>

<script>
import Axios from "axios";
export default {
  created() {
    this.ad = this.$store.getters.adminData.ad;
  },
  data() {
    return {
      loading: false,
      ad: null,
    };
  },
  methods: {
    publishAd() {
      this.loading = true;
      Axios.put("/publishAd", { ad: this.ad }).then((res) => {
        this.loading = false;
        if (res.data.done) {
          this.$store.dispatch("writemessage", "تم النشر بنجاح");
        } else {
          this.$store.dispatch("writemessage", res.data.err);
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.ad {
  padding: 15px 5px;
  background: linear-gradient(
    rgba(29, 28, 28, 0.218),
    rgba(163, 194, 207, 0.259),
    rgba(16, 16, 17, 0.198)
  );
}
</style>
